const getAllChapters = function (db) {
  const chapterName = this.$store.state.username + 'chapter'
  let transaction = db.transaction([chapterName], 'readwrite')
  let objectStore = transaction.objectStore(chapterName)
  let allchapters = []

  return new Promise(resolve => {
    objectStore.openCursor().onsuccess = function (event) {  //使用游标遍历
      let cursor = event.target.result
      // console.log(cursor, '游标');
      if (cursor) {
        // console.log(111, cursor.key, cursor.value);
        allchapters.push(cursor.value)
        cursor.continue()
      } else {
        // console.log('没有更多数据了！')
        resolve(allchapters)
      }
    }

    //如果游标遍历出现错误
    objectStore.openCursor().onerror = function (event) {
      console.dir(event)
    }
  })

}

export {getAllChapters}
